.page-footer__navigation-list a {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 767px) {
  .page-footer__navigation-list a {
    font-size: 16px;
    line-height: 19px;
  }
}

.page-footer__social-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 11px;
}
@media (max-width: 767px) {
  .page-footer__social-list {
    column-span: all;
  }
}
.page-footer__social-list li {
  background-color: #ffffff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
}
.page-footer__social-list svg {
  color: #030a1a;
}
.page-footer__social-list a {
  display: block;
}

